@import "../../index.scss";

.gpt3__cta {
  padding: 2rem;
  margin: 4rem;

  border-radius: 1rem;
  background: var(--gradient-bar);

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include devices(mobile) {
    margin: 4rem 2rem;
    flex-direction: column;
  }

  &-content {
    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 12px;
      line-height: 30px;
      color: #0e0e0e;
    }

    h3 {
      font-family: var(--font-family);
      font-weight: 800;
      font-size: 24px;
      line-height: 45px;
      color: #000000;

      @include devices(mobile) {
        font-size: 18px;
        line-height: 32px;
      }
    }
  }

  &-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;

    @include devices(mobile) {
      margin: 2rem 0 0;
    }

    button {
      min-width: 150px;
      padding: 0.5rem 1rem;
      background: #000000;
      border-radius: 40px;
      border: none;
      outline: none;

      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 30px;

      color: #ffffff;

      cursor: pointer;

      @include devices(mobile) {
        font-size: 14px;
        line-height: 28px;
      }
    }
  }
}
